// src/app/me/page.tsx
"use client";

import { useEffect, useMemo, useState } from "react";
import AppShell from "@/components/shell/AppShell";
import AuthGuard from "@/components/AuthGuard";
import { useToast } from "@/components/Toast";
import { motion } from "framer-motion";

import { ProgressHeader } from "@/components/me/ProgressHeader";
import { CourseList, type UICourse } from "@/components/me/CourseList";

/** ============================ DATA ADAPTER ============================
 * Ajuste APENAS esta função para mapear seu shape real de dados
 * para o shape de UI esperado pelos componentes (UICourse / header).
 * Mantive defaults defensivos para não quebrar enquanto você adapta.
 */
async function fetchDashboard(): Promise<{
  userName: string;
  points: number;
  level: number;
  nextTarget: number; // pontos para próximo nível
  completedModules: number;
  totalModules: number;
  courses: UICourse[];
}> {
  // Exemplo: se você já tiver endpoint próprio, troque por fetch('/api/me/dashboard')
  // const res = await fetch("/api/me/dashboard", { cache: "no-store" });
  // const json = await res.json();

  // ---- MOCK SEGURO (remova quando ligar nos seus dados) ----
  return {
    userName: "Anderson Bezerra",
    points: 100,
    level: 2,
    nextTarget: 200,
    completedModules: 10,
    totalModules: 18,
    courses: [
      {
        id: "git",
        title: "Git e GitHub",
        subtitle: "Fluxo básico de versionamento",
        totalModules: 3,
        completedModules: 0,
        items: [
          { id: "git-1", title: "Configuração inicial", status: "pending", points: 10 },
          { id: "git-2", title: "Commits e Branches", status: "pending", points: 10 },
          { id: "git-3", title: "Pull Request (PR)", status: "pending", points: 10 },
        ],
      },
      {
        id: "next",
        title: "Next.js 15 — Fundamentos",
        subtitle: "App Router, SSR, rotas e dados",
        totalModules: 6,
        completedModules: 6,
        items: [
          { id: "next-1", title: "App Router e Layouts", status: "done", points: 10 },
          { id: "next-2", title: "Rotas dinâmicas", status: "done", points: 10 },
          { id: "next-3", title: "Data Fetching (cache/revalidate)", status: "done", points: 10 },
          { id: "next-4", title: "Server/Client Components", status: "done", points: 10 },
          { id: "next-5", title: "Middleware e redirects", status: "done", points: 10 },
          { id: "next-6", title: "Deploy e Env Vars", status: "done", points: 10 },
        ],
      },
    ],
  };
  // ---------------------------------------------------------------------
}

export default function MePage() {
  const { push } = useToast();
  const [loading, setLoading] = useState(true);
  const [err, setErr] = useState<string | null>(null);

  const [userName, setUserName] = useState("Aluno");
  const [points, setPoints] = useState(0);
  const [level, setLevel] = useState(1);
  const [nextTarget, setNextTarget] = useState(0);
  const [completedModules, setCompletedModules] = useState(0);
  const [totalModules, setTotalModules] = useState(0);
  const [courses, setCourses] = useState<UICourse[]>([]);

  useEffect(() => {
    let alive = true;
    (async () => {
      setLoading(true);
      try {
        const data = await fetchDashboard();
        if (!alive) return;
        setUserName(data.userName ?? "Aluno");
        setPoints(data.points ?? 0);
        setLevel(data.level ?? 1);
        setNextTarget(data.nextTarget ?? 0);
        setCompletedModules(data.completedModules ?? 0);
        setTotalModules(data.totalModules ?? 0);
        setCourses(data.courses ?? []);
      } catch (e: any) {
        setErr(e?.message ?? "Falha ao carregar");
      } finally {
        setLoading(false);
      }
    })();
    return () => {
      alive = false;
    };
  }, []);

  const percent = useMemo(() => {
    if (!totalModules) return 0;
    return Math.min(100, Math.round((completedModules / totalModules) * 100));
  }, [completedModules, totalModules]);

  function handleComplete(itemId: string, courseId: string, pointsToAdd: number) {
    // Atualiza estado local
    setCourses((prev) =>
      prev.map((c) =>
        c.id !== courseId
          ? c
          : {
              ...c,
              items: c.items.map((it) => (it.id === itemId ? { ...it, status: "done" } : it)),
              completedModules: Math.min(c.totalModules, c.completedModules + 1),
            }
      )
    );
    setCompletedModules((v) => v + 1);
    setPoints((p) => p + (pointsToAdd || 0));

    // Toast
    push(`+${pointsToAdd || 0} pts adicionados`);
    // TODO: aqui você pode chamar sua API para persistir
    // fetch("/api/modules/complete", { method: "POST", body: JSON.stringify({ itemId, courseId }) })
  }

  return (
    <AuthGuard>
      <AppShell>
        <div className="px-5 md:px-8 mx-auto w-full max-w-6xl py-8 md:py-10">
          {/* Header compacto com gradient minimalista */}
          <ProgressHeader
            userName={userName}
            points={points}
            level={level}
            nextTarget={nextTarget}
            percent={percent}
            completedModules={completedModules}
            totalModules={totalModules}
            loading={loading}
          />

          {/* Lista de cursos */}
          <section className="mt-10">
            <div className="flex items-center justify-between mb-4">
              <h2 className="text-lg md:text-xl font-semibold text-white/90">Cursos ativos</h2>
            </div>

            <CourseList
              courses={courses}
              onComplete={handleComplete}
            />
          </section>

          {/* Erro */}
          {err && (
            <motion.div
              initial={{ opacity: 0, y: 6 }}
              animate={{ opacity: 1, y: 0 }}
              className="mt-6 text-sm text-red-400"
            >
              {err}
            </motion.div>
          )}
        </div>
      </AppShell>
    </AuthGuard>
  );
}
